import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import PouchDB from 'pouchdb';
import { AlertController } from 'ionic-angular';

/*
  Generated class for the DatabaseProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class DatabaseProvider {

  private _DB: any;
  private success: boolean = true;

  constructor(public http: HttpClient,
    public alertCtrl: AlertController) {
    console.log('Hello DatabaseProvider Provider');
    this.initialiseDB();
  }

  initialiseDB() {
    this._DB = new PouchDB('Students');
  }



  addStudent(nom, prenom, age, quartier, sexe, contact, bac, lycee, nomParent, contactParent, image) {
    var timeStamp = new Date().toISOString(),
      base64String = image.substring(23),
      Student = {
        _id: timeStamp,
        nom: nom,
        prenom: prenom,
        age: age,
        quartier: quartier,
        sexe: sexe,
        contact: contact,
        bac: bac,
        lycee: lycee,
        nomParent: nomParent,
        contactParent: contactParent,
        _attachments: {
          "character.jpg": {
            content_type: 'image/jpeg',
            data: base64String
          }
        }
      };

    return new Promise(resolve => {
      this._DB.put(Student).catch((err) => {
        this.success = false;
      });

      resolve(true);

    });
  }



  updateStudent(id, nom, prenom, age, quartier, sexe, contact, bac, lycee, nomParent, contactParent, image, revision) {
    var base64String = image.substring(23),
      Student = {
        _id: id,
        _rev: revision,
        nom: nom,
        prenom: prenom,
        age: age,
        quartier: quartier,
        sexe: sexe,
        contact: contact,
        bac: bac,
        lycee: lycee,
        nomParent: nomParent,
        contactParent: contactParent,
        _attachments: {
          "character.jpg": {
            content_type: 'image/jpeg',
            data: base64String
          }
        }
      };

    return new Promise(resolve => {
      this._DB.put(Student)
        .catch((err) => {
          this.success = false;
        });

      if (this.success) {
        resolve(true);
      }
    });
  }



  retrieveStudent(id) {
    return new Promise(resolve => {
      this._DB.get(id, { attachments: true })
        .then((doc) => {
          var item = [],
            dataURIPrefix = 'data:image/jpeg;base64,',
            attachment;

          if (doc._attachments) {
            attachment = doc._attachments["character.jpg"].data;
          }

          item.push(
            {
              id: id,
              rev: doc._rev,
              nom: doc.nom,
              prenom: doc.prenom,
              age: doc.age,
              quartier: doc.quartier,
              sexe: doc.sexe,
              contact: doc.contact,
              bac: doc.bac,
              lycee: doc.lycee,
              nomParent: doc.nomParent,
              contactParent: doc.contactParent,
              image: dataURIPrefix + attachment
            });
          resolve(item);
        })
    });
  }




  retrieveStudents() {
    return new Promise(resolve => {
      this._DB.allDocs({ include_docs: true, descending: true, attachments: true }, function (err, doc) {
        let k,
          items = [],
          row = doc.rows;

        for (k in row) {
          var item = row[k].doc,
            dataURIPrefix = 'data:image/jpeg;base64,',
            attachment;

          if (item._attachments) {
            attachment = dataURIPrefix + item._attachments["character.jpg"].data;
          }

          items.push(
            {
              id: item._id,
              rev: item._rev,
              nom: item.nom,
              prenom: item.prenom,
              age: item.age,
              quartier: item.quartier,
              sexe: item.sexe,
              contact: item.contact,
              bac: item.bac,
              lycee: item.lycee,
              nomParent: item.nomParent,
              contactParent: item.contactParent,
              image: attachment
            });
        }
        resolve(items);
      });
    });
  }



  removeStudent(id, rev) {
    return new Promise(resolve => {
      var Student = { _id: id, _rev: rev };

      this._DB.remove(Student)
        .catch((err) => {
          this.success = false;
        });

      if (this.success) {
        resolve(true);
      }
    });
  }



  errorHandler(err) {
    let headsUp = this.alertCtrl.create({
      title: 'Heads Up!',
      subTitle: err,
      buttons: ['Got It!']
    });

    headsUp.present();
  }


}


