import { Component } from '@angular/core';
import { NavController, ToastController } from 'ionic-angular';
import { DatabaseProvider } from '../../providers/database/database';
import { File } from '@ionic-native/file';
import { EmailComposer } from '@ionic-native/email-composer';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  public hasStudents: boolean = false;
  public students: any;

  constructor(public navCtrl: NavController,
    public toastCtrl: ToastController,
    public DB: DatabaseProvider, public file:File, private emailComposer: EmailComposer) {

  }

  ionViewWillEnter() {
    this.displayStudents();
  }

  displayStudents() {
    this.DB.retrieveStudents().then((data) => {
      let existingData = Object.keys(data).length;
      if (existingData !== 0) {
        this.hasStudents = true;
        this.students = data;
       // alert(this.file.externalRootDirectory);
      }
      else {
        console.log("we get nada!");
      }
    });
  }

  addCharacter() {
    this.navCtrl.push('AddPage');
  }

  viewCharacter(param) {
    this.navCtrl.push('AddPage', param);
  }

  testEmail() {
    let text = "ID, Nom, Prénom, Âge, Sexe, Quartier, Contact, Nom Parent, Contact Parent, Bac, Lycée, \n";
    for(let i=0; i<this.students.length; i++){
      text = text.concat(`${this.students[i].rev}, ${this.students[i].nom}, ${this.students[i].prenom}, ${this.students[i].age}, ${this.students[i].sexe}, ${this.students[i].quartier}, ${this.students[i].contact}, ${this.students[i].nomParent}, ${this.students[i].contactParent}, ${this.students[i].bac}, ${this.students[i].lycee},\n`);
    }
    let filePath = this.file.externalRootDirectory;  
    this.file.writeFile(filePath, 'studentData.csv', text, {replace: true})
        .then(() => {      
          let email = {
            to: 'isficb@gmail.com',
            attachments: [
              filePath + 'studentData.csv'
            ],
   
            subject: 'Envoi de données '+new Date().toISOString(),
            body: '...',
            isHtml: true
          };
          this.emailComposer.open(email);
          //this.sendNotification('Email envoyé avec succès !')
   
        })
        .catch((err) => {
          console.error(err);
        });
   
    }

    sendNotification(message): void {
      let notification = this.toastCtrl.create({
        message: message,
        duration: 3000
      });
      notification.present();
    }

}


